<template>
  <div class="home">
    <page-header :title="''" :timestr="'2020-02-14'" :author="''"></page-header>
    <section class="home-banner">
      <img src="@/assets/images/home_banner.jpg" alt="banner">
    </section>
    <section class="padding">
      <cross-square>
        <div slot="content">
          <p style="font-size: .3rem;font-weight: bold;text-align: center;"></p>
          <p></p>
        </div>
      </cross-square>
    </section>
    <section class="padding home-slogans">
      <div><span class="home-slash">///</span><span class="home-slash">///</span></div>
      <div><span class="home-slash">///</span><span class="home-slash">///</span></div>
      <div><span class="home-slash">///</span><span class="home-slash">///</span></div>
    </section>
    <section class="padding" style="margin-top: .5rem;">
      <bg-square title="">
        <div slot="content">
          <div class="home-buttons">
            <div class="home-btn" @click="goManager"></div>
            <div class="home-btn" @click="goManager"></div>
          </div>
          <div class="home-buttons">
            <div class="home-btn" @click="goManager"></div>
            <div class="home-btn" @click="goManager"></div>
          </div>
        </div>
      </bg-square>
    </section>
    <section class="padding" style="margin-top:.6rem;">
      <bg-square title="">
        <div slot="content">
          <div class="home-buttons">
            <div class="home-btn" @click="goManager"></div>
            <div class="home-btn" @click="goManager"></div>
          </div>
          <div class="home-buttons">
            <div class="home-btn" @click="goManager"></div>
            <div class="home-btn" @click="goManager"></div>
          </div>
          <div class="home-buttons">
            <div class="home-btn" @click="goManager"></div>
            <div class="home-btn" @click="goManager"></div>
          </div>
          <div class="home-buttons">
            <div class="home-btn" @click="goManager"></div>
            <div class="home-btn" @click="goManager"></div>
          </div>
        </div>
      </bg-square>
    </section>
  </div>
</template>

<script>
import pageHeader from './common/PageHeader'
import crossSquare from './common/CrossSquare'
import bgSquare from './common/BgSquare'

export default {
  components: { pageHeader, crossSquare, bgSquare },
  beforeRouteEnter (to, from, next) {
    document.title = ''
    next()
  },
  methods: {
    goManager () {
      this.$router.push({ name: 'Manager' })
    }
  }
}
</script>

<style lang="scss" scoped>
.home {
  padding-bottom: .3rem;
  &-banner {
    padding: 0 .3rem;
    & > img {
      width: 100%;
    }
  }
  &-slogans {
    font-size: .3rem;
    font-weight: bolder;
    text-align: center;
  }
  &-slash {
    display: inline-block;
    color: #d32f2f;
    &:nth-child(2n) {
      margin-left: .3rem;
    }
    &:nth-child(2n+1) {
      margin-right: .3rem;
    }
  }
  &-buttons {
    display: flex;
    justify-content: space-around;
    margin-bottom: .1rem;
    & > div {
      width: 42%;
      text-align: center;
    }
  }
  &-btn {
    height: .8rem;
    line-height: .8rem;
    margin-bottom: .2rem;
    color: #fff;
    font-size: .3rem;
    font-weight: bold;
    background-color: #2d8cf0;
    border-radius: .1rem;
    cursor: pointer;
    &:hover {
      opacity: .85;
    }
  }
  .padding {
    padding: .3rem .3rem 0;
  }
  .txt-center {
    text-align: center;
  }
}
</style>



