<template>
  <div class="manager">
    <page-header :title="''" :timestr="'2020-02-14'" :author="''"></page-header>
    <section class="manager-banner">
      <img src="@/assets/images/manager_banner.jpg" alt="banner">
    </section>
    <section class="padding">
      <cross-square>
        <div slot="content">
          <p class="center" style="font-size: .3rem;font-weight: bold;"></p>
          <p class="center"></p>
          <p class="center"> TA  call TA</p>
        </div>
      </cross-square>
    </section>
    <section class="padding">
      <person v-for="item in users" :key="item.id" :userInfo="item" class="manager-person"></person>
    </section>
  </div>
</template>

<script>
import pageHeader from './common/PageHeader'
import crossSquare from './common/CrossSquare'
import Person from './common/Person'

export default {
  components: { pageHeader, crossSquare, Person },
  beforeRouteEnter (to, from, next) {
    document.title = ''
    next()
  },
  data () {
    return {
      users: [
        {
          id: '1_1',
          name: '',
          sex: 'male',
          slogan: '',
          title: '',
          certificates: ['fund'],
          imgSrc: require('@/assets/images/user_1.jpg'),
          location: 'XXX',
          tel: 17801024227
        },
        {
          id: '1_2',
          name: '',
          sex: 'female',
          slogan: '',
          title: '',
          certificates: ['financial', 'fund'],
          imgSrc: require('@/assets/images/user_2.jpg'),
          location: 'XXXXX',
          tel: 17801024227
        },
        {
          id: '1_3',
          name: '',
          sex: 'male',
          slogan: '',
          title: '',
          certificates: ['fund'],
          imgSrc: require('@/assets/images/user_3.jpg'),
          location: 'XXX',
          tel: 17801024227
        },
        {
          id: '1_4',
          name: '',
          sex: 'female',
          slogan: '',
          title: '',
          certificates: ['financial', 'fund'],
          imgSrc: require('@/assets/images/user_4.jpg'),
          location: 'XXXXX',
          tel: 17801024227
        }
      ]
    }
  },
  methods: {
    goHome () {
      this.$router.go(-1)
    }
  }
}
</script>
<style lang="scss" scoped>
.manager {
  padding-bottom: .3rem;
  &-banner {
    padding: 0 .3rem;
    & > img {
      width: 100%;
    }
  }
  &-person {
    &:last-child {
      border-bottom: none;
      margin-bottom: 0;
    }
  }
  .padding {
    padding: .3rem .3rem 0;
  }
  .center {
    text-align: center;
  }
}
</style>



